import logo from './logo.svg';
import React from 'react'
import './App.css';
import circuits from './Circuits.png'
import connectFour from './Connect4.png'
import qupcakes from './Qupcakes.png'
import YoutubeEmbed from './Components/YoutubeEmbed'
import CaesarShift from './Components/CaesarShift'
import Bits from './Components/Bits'
import OneWay from './Components/OneWay'
import Quiz from './Components/Quiz'
import { LoremIpsum } from "react-lorem-ipsum";


export class App extends React.Component{




    string_comp(A, B){
        let proc_s = (s) => {return s.replace(/\s/g,'').toUpperCase()}
        return proc_s(A) === proc_s(B)
    }

    number_comp(a, b){
        let fa =  parseFloat(a)
        let fb = parseFloat(b)
        return (!isNaN(fa) && !isNaN(fb) && Math.abs(fa - fb) < 0.001)
    }
    constructor(props){
        super(props)
        this.myRef = React.createRef();


        this.title = "Welcome to Quander!"
        this.intro = (<div>
                <h1 className="Main"> {this.title}</h1>
                    <br/>
                    <p className="Main">
                        <LoremIpsum
                            p={2}
                        />
                    </p>
                    <br/>
                    <YoutubeEmbed embedId="pxSk9VaLXA0" />
                    <br/>
            </div>)


        


        this.state = {
            completed_segments : {},
        }
    }


    makeSection(title, paragraph, link, img, left) {
        let floatDir = left ? 'left' : 'right'
        return (
            <div className="Main" style={{minHeight:400}}>
                <h1 className="Main">{title}</h1>
                <img src={img} width={300} style={{float:floatDir, margin:'15px'}}/>
                <p className="Main">
                        {paragraph}
                </p>
                <a className="Main" href={link}>Play now!</a>
            </div>)
    }



    gen_quiz(i){
        let callback = (ans) => {return this.grade_quiz(i, ans)}
        return (
            <div>
                <Quiz question={this.quizes[i][0]} correct={this.state.quizes_correct[i]} callback={callback} />
                <br/>
                <br/>
                <br/>
                <br/>
            </div>)
    }

    render(){
        let circuitsText = 'Help the EPIQC research center continue their research by solving pattern base puzzles!'
        let connectText = 'Enjoy a classic board game with a quantum twist! Can you line up your pieces while dealing with the uncertainty?!?'
        let qupcakesText = "Orders Up! Deliver as many 'qupcakes' as possible while using gizmos to make sure every customer gets their favorite flavor!"
        let output = [
            this.intro,
            this.makeSection('Crazy Circuits', circuitsText, "https://chord.cs.uchicago.edu/Quander/Circuits/", circuits, true),
            this.makeSection('Queue Bits', connectText, "https://chord.cs.uchicago.edu/Quander/ConnectFour/", connectFour),
            this.makeSection('Qupcakes', qupcakesText, "https://chord.cs.uchicago.edu/Quander/QuantumCafe/", qupcakes, true),
        ]



        return (
            <div className="App">
                <div className="Main">
                    {output}
                </div>

            </div>
        )
    }


}

